<template>
  <div class="container">
    <el-dialog
      class=""
      :title="$t('IDC.organization.dialogProduct.dialogTitle') + ''"
      :visible.sync="visible"
      width="800px"
      :close-on-click-modal="false"
      :before-close="cancel"
    >
      <div class="container-condition">
        <el-form
          size="small"
          :class="['add-form', 'form-flex-container']"
          ref="condition"
          :model="condition"
          label-position="top"
          @submit.prevent.native
        >
          <el-form-item :label="$t('IDC.organization.dialogProduct.productName') + ''" prop="operatorName">
            <el-input
              size="small"
              v-model.trim="condition.productName"
              @keyup.native.enter="() => {}"
              placeholder="Please enter"
              @focus="() => {}"
            />
          </el-form-item>
        </el-form>
        <div class="condition-options">
          <el-button plain size="small" @click="reset">{{ $t('common.button.reset') }}</el-button>
          <el-button plain size="small" @click="search" v-preventReClick type="primary"
            >{{ $t('common.button.search') }}
          </el-button>
        </div>
      </div>
      <div class="container-products">
        <table-page
          :columns="columns"
          :get-table-data="loadData"
          :init-checks="checks"
          :is-check="true"
          :is-search="searching"
          :is-single-check="true"
          :pagination="pagination"
          @selection-change="onSelectionChange"
          @single-selection-change="onSingleSelectionChange"
          class=""
          id-key="productIndex"
          id="table"
          size="small"
        >
        </table-page>
      </div>
      <span slot="footer" class="dialog-footer">
        <div class="option-buttons">
          <el-button size="small" @click="confirm" v-preventReClick type="primary"
            >{{ $t('common.button.confirm') }}
          </el-button>
          <el-button plain size="small" @click="cancel">{{ $t('common.button.cancel') }}</el-button>
        </div>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import TablePage from '@/components/tablePage/index.vue';

export default {
  name: 'DialogProduct',
  components: { TablePage },
  mixins: {},
  props: {
    check: {
      type: Boolean,
      default: false,
    },
    checked: {
      type: Object,
      default: () => {
        return {};
      },
    },
    singleCheck: {
      type: Boolean,
      default: false,
    },
    visible: {
      type: Boolean,
      default: false,
    },
    dataLoader: {
      type: Function,
      default: () => {},
    },
  },
  data() {
    return {
      checks: [],
      condition: {
        productName: '',
      },
      searching: false,
    };
  },
  computed: {
    columns() {
      return [
        {
          label: this.$t('IDC.organization.dialogProduct.productName'),
          prop: 'productName',
          align: 'left',
          'sort-method': (a, b) => {
            return a.productName > b.productName ? 1 : -1;
          },
        },
        {
          label: this.$t('common.label.description'),
          prop: 'description',
          align: 'left',
        },
      ];
    },
    pagination() {
      return {
        pageIndex: 1,
        pageSize: 10,
        layout: 'total, prev, pager, next',
      };
    },
  },
  watch: {
    checked(value) {
      if (value && value.productName) {
        this.checks = [{ productIndex: value.productId, productName: value.productName }];
      } else {
        this.checks = [];
      }
    },
  },
  filter: {},
  created: function () {},
  activated: function () {},
  mounted: function () {},
  methods: {
    cancel() {
      this.closeDialog();
    },
    confirm() {
      this.$emit('confirm', this.checks);
      this.closeDialog(false);
    },
    closeDialog(reset = true) {
      if (reset) {
        this.reset(false);
      }
      this.$emit('update:visible', false);
    },
    async loadData(param) {
      Object.assign(param, this.condition);
      if (this.dataLoader) {
        let res;
        try {
          res = await this.dataLoader(param);
        } catch (e) {
          res = {
            header: {
              success: false,
            },
          };
        } finally {
          this.searching = false;
        }
        return res;
      }
    },
    onSelectionChange(value) {
      this.checks = value;
    },
    onSingleSelectionChange(value) {
      this.checks = [value];
    },
    reset(reload = true) {
      this.condition.productName = '';
      this.pagination.pageIndex = 1;
      if (reload) {
        this.search();
      }
    },
    search() {
      this.searching = true;
    },
  },
};
</script>

