<template>
  <div class="group-list-config business-portal-page">
    <!-- header -->
    <portal-header :projectObj="titleConfig"> </portal-header>
    <!-- search -->
    <portal-form
      ref="resForm"
      :form-data="condition"
      :form-config-list="formConfigList"
      :formRules="groupRules"
      :searchBtnLoading="searchLoading"
      :searchBtnDisabled="!hasPerm('IC_IDENTITY_GROUP_SEARCH_BTN')"
      @reset-btn-click="reset"
      @search-btn-click="search"
    >
    </portal-form>
    <!-- columns -->
    <div class="content">
      <div style="padding-bottom: 16px" class="createDiv">
        <el-button
          type="primary"
          size="small"
          icon="el-icon-plus"
          :disabled="!hasPerm('IC_IDENTITY_CREATEGROUP_BTN')"
          @click="addCreate"
        >
          Create
        </el-button>
        <div>
          <el-select
            class="sortType"
            v-model="sortType"
            :placeholder="$t('common.placeholder.select') + ''"
            v-if="sortField.length > 0"
          >
            <el-option v-for="item in sortListType" :key="item.code" :label="item.name" :value="item.code"> </el-option>
          </el-select>
          <el-select v-model="sortField" :placeholder="$t('common.placeholder.select') + ''" style="width: 200px">
            <el-option v-for="item in sortList" :key="item.code" :label="item.name" :value="item.code"> </el-option>
          </el-select>
        </div>
      </div>
      <div>
        <table-page
          size="small"
          class="table-page"
          ref="group-table"
          :has-pagination="true"
          :columns="columns"
          :pagination="paginationConfig"
          :initializingLoading="false"
          :getTableData="getTableData"
          :is-search="isSearch"
          :sortArr="sortArr"
        >
          <template v-slot:status="scope">
            <span :class="['status-circle', statusStyle(scope.data.row.status)]"></span>
            <span class="status-name">{{ showStatus(scope.data.row.status) }}</span>
          </template>
          <template v-slot:operation="scope">
            <el-button size="mini" type="text" @click="handleDetail(scope.data.row)">{{
              $t('common.button.detail')
            }}</el-button>
          </template>
        </table-page>
      </div>
    </div>
    <dialog-product
      :check="true"
      :checked="{ productId: condition.productId, productName: condition.productName }"
      :data-loader="loadProducts"
      :single-check="true"
      :visible.sync="showProductDialog"
      @confirm="onProductChange"
    >
    </dialog-product>
  </div>
</template>

<script>
import TablePage from '@/components/tablePage';
import PortalHeader from '@/components/portal-header';
import PortalForm from '@/components/portal-form';
import DialogProduct from '@/views/identifier-center/pages/components/dialog-product.vue';
import { queryDCCProduct, queryBusinessGroupList } from '@/views/identifier-center/api/api-group';
import { STATUS_MAP } from '@/views/task-center/page/task-detail/utils/data-dic.js';
import {
  checkFuzzySearchInput,
  nonSpecialCharExceptPercentSignValidateObj,
  len24StrValidateObj,
  len64StrValidateObj,
} from '@/common/validator/common.js';

export default {
  components: { DialogProduct, TablePage, PortalHeader, PortalForm },
  name: 'GroupListPage',
  data() {
    return {
      columns: [
        {
          label: 'Group Code',
          prop: 'groupCode',
          align: 'left',
          'sort-method': (a, b) => {
            return a.groupCode > b.groupCode ? 1 : -1;
          },
        },
        {
          label: 'Group Name',
          prop: 'groupName',
          align: 'left',
          'sort-method': (a, b) => {
            return a.groupName > b.groupName ? 1 : -1;
          },
        },
        {
          'sort-method': (a, b) => {
            return a.status > b.status ? 1 : -1;
          },
          __slotName: 'status',
          align: 'left',
          label: 'Status',
          prop: 'status',
        },
        {
          label: 'Group Product',
          prop: 'productName',
          align: 'left',
          'sort-method': (a, b) => {
            return a.productName > b.productName ? 1 : -1;
          },
        },
        {
          label: 'Operation',
          prop: 'operation',
          align: 'left',
          __slotName: 'operation',
          width: 104,
        },
      ],
      groupRules: {
        groupCode: [len24StrValidateObj, nonSpecialCharExceptPercentSignValidateObj],
        groupName: [len64StrValidateObj, nonSpecialCharExceptPercentSignValidateObj],
      },
      paginationConfig: {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
      },
      sortArr: ['status'],
      showProductDialog: false,
      isSearch: false,
      searchBtnDisabled: false,
      searchBtnLoading: false,
      searchLoading: false,
      condition: {
        groupCode: '%',
        groupName: '%',
        status: '',
        productName: '',
        productId: '',
      },
      sortField: '',
      sortType: 'ASC',
      sortList: [
        {
          name: 'Sort By',
          code: '',
        },
        {
          name: 'Group Code',
          code: 'GROUP_CODE',
        },
        {
          name: 'Group Name',
          code: 'Group_Name',
        },
      ],
      sortListType: [
        {
          name: 'Ascending',
          code: 'ASC',
        },
        {
          name: 'Descending',
          code: 'DESC',
        },
      ],
      searchVisible: false,
      productPopupConf: {
        title: this.$t('IDC.dialogProduct.dialogTitle'),
        label: 'Product Name',
        columns: [
          { label: this.$t('IDC.dialogProduct.productName'), prop: 'productName', align: 'left' },
          { label: this.$t('common.label.description'), prop: 'description', align: 'left' },
        ],
        searchForm: {
          formData: {
            productName: null,
          },
          formConf: [
            {
              bind: {
                clearable: true,
                placeholder: this.$t('common.placeholder.enter'),
              },
              comp: 'el-input',
              event: {
                change: () => {},
              },
              key: 'productName',
              label: this.$t('IDC.dialogProduct.productName'),
              prop: 'productName',
            },
          ],
        },
      },
    };
  },
  computed: {
    formConfigList() {
      return [
        {
          label: 'Group Code',
          key: 'groupCode',
          comp: 'el-input',
          bind: {
            id: 'Name',
            placeholder: this.$t('common.placeholder.enter'),
            'show-word-limit': true,
          },
        },
        {
          label: 'Group Name',
          key: 'groupName',
          comp: 'el-input',
          bind: {
            id: 'Name',
            placeholder: this.$t('common.placeholder.enter'),
            'show-word-limit': true,
          },
        },
        {
          label: 'Status',
          key: 'status',
          comp: 'el-select',
          options: [
            {
              value: '',
              label: 'All',
            },
            {
              value: '03',
              label: 'Active',
            },
            {
              value: '07',
              label: 'Capped',
            },
            {
              value: '06',
              label: 'Closed',
            },
            {
              value: '08',
              label: 'Dormant',
            },
            {
              value: '05',
              label: 'Frozen',
            },
            {
              value: '00',
              label: 'New',
            },
            {
              value: '02',
              label: 'Pending Active',
            },
            {
              value: '04',
              label: 'Suspended',
            },
          ],
          event: {
            change: this.changeType,
          },
          bind: {
            id: 'Name',
            placeholder: this.$t('common.placeholder.enter'),
            'show-word-limit': true,
            clearable: true,
          },
        },
        {
          bind: {
            placeholder: this.$t('common.placeholder.select'),
            clearable: true,
            readonly: true,
          },
          comp: 'el-input',
          key: 'productName',
          label: 'Group Product',
          prop: 'productName',
          slots: [
            {
              name: 'append',
              comp: 'el-link',
              class: 'short-code-icon',
              bind: {
                icon: 'el-icon-more',
                underline: false,
              },
              event: {
                click: () => {
                  this.showProductDialog = true;
                },
              },
            },
          ],
        },
      ];
    },
    titleConfig() {
      return {
        title: 'Group',
        desc: '',
      };
    },
    configRouteType() {
      return this.getConfigRouteType();
    },
    basePath() {
      return `/identity/group/`;
    },
  },
  created() {},
  activated() {},
  methods: {
    addCreate() {
      this.openNewWindow({ path: 'create' });
    },
    search() {
      if (
        (!this.condition.groupCode || this.condition.groupCode === '%') &&
        (!this.condition.groupName || this.condition.groupName === '%') &&
        !this.condition.productId
      ) {
        this.$modal.msgError(this.$t('IDC.group.list.conditionNotice'));
      } else {
        this.searchBtnLoading = true;
        this.isSearch = true;
      }
    },
    reset() {
      this.condition.groupCode = '%';
      this.condition.groupName = '%';
      this.condition.status = '';
      this.condition.productName = '';
      this.condition.productId = '';
      this.sortField = '';
      this.sortType = 'ASC';
      this.$refs['group-table'].tableData = [];
      this.$refs['group-table'].$refs['tableRef'].clearSort();
    },
    handleSearchClick() {},
    handleResetClick() {},
    handleDetail(data) {
      let params = {
        groupCode: data.groupCode,
        identityId: data.identityId,
      };
      this.openNewWindow({ path: 'detail', query: { params: encodeURIComponent(JSON.stringify(params)) } });
    },
    openVisible() {
      this.searchVisible = true;
    },
    changeType() {},
    onProductChange(data) {
      if (data && data.length) {
        this.condition.productId = data[0].productIndex;
        this.condition.productName = data[0].productName;
      } else {
        this.condition.productId = '';
        this.condition.productName = '';
      }
    },
    async loadProducts(params) {
      let res = await queryDCCProduct(params);
      if (res.header && res.header.success) {
        res.totalRecords = res.total;
      }
      return res;
    },
    getTableData() {
      let res;
      try {
        let param = {
          groupCode: this.condition.groupCode,
          groupName: this.condition.groupName,
          identitySortTypeDTO: {
            sortField: this.sortField,
            sortType: this.sortType,
          },
          pageNo: this.paginationConfig.pageIndex,
          pageSize: this.paginationConfig.pageSize,
          productId: this.condition.productId,
          status: this.condition.status,
        };
        const checkObj = {
          groupCode: param.groupCode,
          groupName: param.groupName,
          productId: param.productId,
        };
        if (!checkFuzzySearchInput(checkObj)) {
          this.$modal.msgError('Specify at least one search condition of Group Code,Group Name and Group Product.');
          this.isSearch = false;
          this.searchLoading = false;
          return;
        }
        res = queryBusinessGroupList(param);
      } catch (e) {
        res = {
          header: {
            success: false,
          },
        };
      } finally {
        this.searchBtnLoading = false;
        this.isSearch = false;
      }
      return res;
    },
    statusStyle(status) {
      switch (status) {
        case '03':
          return 'notice';
        case '04':
        case '05':
        case '08':
          return 'warning';
        case '06':
          return 'closed';
        default:
          return 'none';
      }
    },
    showStatus(status) {
      return STATUS_MAP[status] || '-';
    },
  },
};
</script>

<style scoped lang="scss">
.notice {
  background-color: $--status_color--notice !important;
}
.warning {
  background-color: $--status_color--warning !important;
}
.closed {
  background-color: $--fill_color--strong !important;
}
.none {
  background-color: $--text_color--white !important;
  border: 1px solid $--line_color--grey !important;
}
.group-list-config {
  background-color: $--text_color--white;
  box-sizing: border-box;
  min-height: 100%;

  /deep/.el-input__inner {
    font-size: 14px;
  }

  /deep/.el-divider--horizontal {
    margin: 24px 0 20px;
  }

  .content {
    padding: 20px;
    border-radius: 0 0 4px 0;
  }

  /deep/ .el-input-group__append {
    font-size: 16px;
    padding-bottom: 6px;
  }
  .createDiv {
    display: flex;
    align-items: center;
    justify-content: space-between;
    .sortType {
      width: 120px;
      margin-right: 25px;
    }
    /deep/ .el-select .el-input .el-select__caret {
      line-height: 32px;
    }
  }
}
</style>
